@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	font-family: "Lato", sans-serif;
	background-color: #1d2c39;
	color: #ffffff;
}

a {
	text-decoration: none;
	color: white;
}

a:hover {
	text-decoration: underline;
}

.App {
	max-width: 800px;
	margin: 0 auto;
}

.header {
	margin: 50px;
}

.app-title {
	text-align: center;
	margin-top: 0;
}

h4 {
	margin: 0;
}

.movies {
	max-width: 1000px;
	margin: 0 auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.movie-card {
	position: relative;
	margin: 5px;
	max-width: 150px;
}

.movie-card__voting {
	position: absolute;
	right: 5px;
	top: 5px;
	background-color: #ff610e;
	color: #1d2c39;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 25px;
	height: 25px;
	font-weight: bold;
	font-size: 11px;
}

.movie-card img {
	width: 100%;
}

.no-image {
	height: 225px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.5);
	color: #000000;
}

.movie-card__info {
	display: flex;
	justify-content: space-between;
}

.search {
	margin: 20px;
	display: flex;
	justify-content: center;
}

.search-input {
	color: white;
	background: transparent;
	padding: 10px;
	border-radius: 5px;
	border: 2px solid white;
}

.search-button {
	background-color: #ff610e;
	color: white;
	font-weight: bold;
	padding: 10px;
	border-radius: 50%;
	margin-left: 10px;
}

.movie-detail {
	display: flex;
}

.movie-detail__infos {
	margin-left: 20px;
}

